

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ffffff;
  
}

.app_class{
  
  margin-left: 0;
  margin-right: 0;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  
  
}

header {
  background: #000;
  color: #f57d1b;
  text-align: center;
  font-family: "terminator";
  padding-top: 1%;
  
}
.main {
  margin: 0;
  flex: 1;
}
.reg_form{
  display: flex;
  flex-direction: column;
  padding-top: 1%;
  
}
.container{
  display: flex;
  flex-direction: column;
}

.ref{
  color: #000;
  text-decoration: none;
}
.ref:hover{
  color: #000;
  text-decoration: underline;
}
.registerbtn{
  width: 50%;
  background: #242424;
  color: #f57d1b;
  border-radius: 8px;
  font-weight: bold;
  margin-top: 1%;
  
}
.registerbtn:hover{
  background: #424242;
}
.loginbtn{
  width: 50%;
  background: #242424;
  color: #f57d1b;
  border-radius: 8px;
  font-weight: bold;
  margin-top: 1%;
}
.loginbtn:hover{
  background: #424242;
}
.loged_username{
  padding-left: 1%;
}
.logoutbtn{
  width: 50%;
  background: #242424;
  color: #f57d1b;
  border-radius: 8px;
  font-weight: bold;
}
.logoutbtn:hover{
  background: #424242;
}
.timer_menu{
  display: flex;
  flex-direction: column;
  margin-top: 10%;
  margin-bottom: 5%;
  text-align: center;
  align-items: center;
}
.currentTaskName{
  
  border: none;
  outline: none;  
  text-align: center;
  width: 100%;
  font-family: "rodchenkoctt";
  
}


.currentTimer{
  font-family: "rodchenkoctt";
  color: #000;
}
.currentTaskName::-moz-placeholder { color: #000; opacity: .8; font-family: "roboto-thin";}
.currentTaskName::-webkit-input-placeholder { color: #000; opacity: .8; font-family: "roboto-thin";}
.currentTaskName:focus:-moz-placeholder { color: #000; opacity: 0; }
.currentTaskName:focus::-webkit-input-placeholder { color: #000; opacity: 0 }

.timer_button{
  width: 30%;
  background: #242424;
  color: #f57d1b;
  border-radius: 8px;
  margin-top: 1%;
  font-family: "terminator";
}
.timer_button:hover{
  background: #424242;
}
.taskSelect {
  width: 70%;
  padding-top: 1%;
  margin-top: 1%;
  background: #242424;
  color: #f57d1b;
  text-align: center;
  cursor: pointer;
  border-radius: 8px;
  font-family: "terminator";
}


.taskSelect:hover  .select {
  display:block;
  border-radius: 0 0 8px 8px;
}

.select:hover {
  display:block;
  background: #424242;
  
}

.select{
  
  display:none;
  overflow-x: hidden;
}
.select:empty {
  height: 0;
}
.saved_tasks {
  margin-top: 3%;
  margin-bottom: 3%;
}
.saved_task {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 1%;
  margin-bottom: 1%;
}

.saved_task_name{
  
  border: none;
  outline: none; 
  text-align: right; 
  width: 30%;
  
}
.saved_task_time{
  width: 125px;
  margin-bottom: 0;
  font-family: "rodchenkoctt";
  text-align: right;
}
.delete_button{
  background: #242424;
  color: #f57d1b;
  border-radius: 8px;
  font-family: "terminator";
}
.delete_button:hover{
  background: #424242;
}
footer {
  background: #000;
  color: #f57d1b;
  padding-top: 1%;
  text-align: center;
  
  
  
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



@font-face {
    font-family: "rodchenkoctt";
    src: url("./fonts/rodchenkoctt.ttf");
}
@font-face {
  font-family: "roboto-thin";
  src: url("./fonts/roboto-thin.ttf");
}
@font-face {
  font-family: "terminator";
  src: url("./fonts/terminator.ttf");
}
